import { Link } from "react-router-dom";
import Navs from "./HeaderContent/Navs";

const Header = () => {
  const NavItems = [
    { address: "/product", name: "Product" },
    { address: "/category", name: "Category" },
    { address: "/about", name: "About Us" },
    { address: "/contact", name: "Contact Us" },
    { address: "/cart", name: "Cart"}
  ];
  return (
    <section className="header_menu" id="header_menu">
      <div className="container-fluid px-0 shadow">
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
          <Link className="navbar-brand" to="/">
            Navbar
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item active">
                <Link className="nav-link" to="/">
                  Home <span className="sr-only">(current)</span>
                </Link>
              </li>
              {NavItems.map((item) => (
                <Navs item={item} key={item.name} />
              ))}
            </ul>
            <form className="form-inline my-2 my-lg-0">
              <input
                className="form-control mr-sm-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button
                className="btn btn-outline-success my-2 my-sm-0"
                type="submit"
              >
                Search
              </button>
            </form>
          </div>
        </nav>
      </div>
    </section>
  );
};

export default Header;
